:root {
  --link-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  height: 100%;
  margin-top: 0;
  padding: 0 50px;
  display: flex;
}

.nav {
  margin: 0;
  padding: 0;
}

.nav__list {
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: right;
  margin-left: 20px;
  font-size: 12px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  text-align: right;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  border-radius: 8px;
  height: 4px;
  max-height: 60px;
}

.logo_link {
  height: 40px;
  display: block;
}

.logo {
  width: 40px;
  height: 40px;
}

.is-active {
  color: #00acdc;
  border-radius: 8px;
  height: 4px;
  max-height: 60px;
  padding-bottom: 10px;
  bottom: 0;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  max-height: 60px;
  margin: 0;
  padding: 0 auto;
  display: block;
  position: absolute;
  bottom: 0;
}

